html { height: 100%; }

img {
	max-width:100%;
}

img + em { 
	display: block;
	text-align: center;
}

a.img-link + em { 
	display: block;
	text-align: center;
}

iframe{
	width: 100%;
	height:400px;
}

body {
	font-family: system-ui, sans-serif;
	-webkit-font-smoothing: antialiased;
    text-rendering: optimizeLegibility;
	line-height:1.5;
	font-size: 1rem;
	min-height: 100%;
	margin: 0;
}

.wrapper {
	max-width: 640px;
	margin: 0 auto;
	padding: 4rem 1rem;
}

h1,h2,h3,h4,h5 {
	line-height: 1;
	margin: 1rem 0;
	font-weight: 600;
}

figure.highlight code {
	display: block;
	overflow-x: auto;
	white-space: pre-wrap;
}

figure.highlight {
	margin-left: 0px;
	margin-right: 0px;
}

div.highlighter-rouge code {
	display: block;
	overflow-x: auto;
	white-space: pre;
	padding: 1em;
}
/**************************************/
/*             HOME                   */
/**************************************/

div.homenav p {
	font-size: 2.5rem;
}

div.homenav a:visited {
	color: black
}

div.homenav a {
	text-decoration: none;
}

/**************************************/
/*            PROJECTS                */
/**************************************/
table.projindex td {
	padding: 0.35rem;
}

/**************************************/
/*             CONTENT/SCROLL         */
/**************************************/
table.content td.date {
        display: inline-block;
        min-width: 160px;
        margin: 0rem;
	color: gray;
}
table.content td.image, table.content td.text {
        display: inline-block;
        width: 100%;
        margin: 0.25rem 0rem 3rem 0rem;
}

/**************************************/
/*           Images                   */
/**************************************/
.wrapper.images {
	max-width: 100%;	
	padding: 0;
}

table.images {
	text-align: center;
}

table.images tr { 
	display: inline;
	margin: 0;
	padding: 0;
	vertical-align: text-top;
}
table.images td.date {
        display: none;
}

table.images img {
	max-height: 400px;
}

table.images td.image {
	display: inline-block;
	margin: 1rem;
}



/**************************************/
/*             NAV                    */
/**************************************/

table.nav {
	margin: 1rem 0px 0px 0px;
	width: 85%;
} 

td.nav_buton.prev{
	text-align: left;
}

td.nav_button.next {
	text-align: right; 
}

main {
	display:flex;
	flex-wrap:wrap;
	margin:2rem auto;
	padding:1rem;
}
